import React from 'react'
import * as bs from 'react-bootstrap'
import AppContext from './context'
import { useLocation } from 'react-router-dom'
import { useEffect } from 'react'
import CampaignCard from './campaignCard'
import { Link } from 'react-router-dom'

const NUM_COLUMNS = 4

function SearchResults(props) {
    return (
        <bs.Container>
            <SearchController />
        </bs.Container>
    )
}
export default SearchResults


const SearchController = props => {
    const context = React.useContext(AppContext)
    const location = useLocation();

    let campaignsArray = Object.values(context.campaigns)

    var results = []

    useEffect(() => {
        console.log(location.state.detail)
        results = location.state.detail;        
    })

    // Get the search results from the api
    var apiResults = (location.state.detail)

    // Filter the results to the correct campaigns
    for (var item of apiResults) {
        for (var itemInner of campaignsArray) {
            if (item.campaign_id === itemInner.campaign_id) {
                // Push it to the array
                results.push(itemInner);
            }
        }
    }

    // slice the products into an array of arrays (e.g. a list of rows of 4 products each)
    const rows = []
    for (let i = 0; i < results.length; i += NUM_COLUMNS) {
        rows.push(results.slice(i, i + NUM_COLUMNS))
    }

    return (
        <div>
        <bs.Row>
            <bs.Col md="3">
                <div className="p-2" style={{"backgroundColor": "#ff6f69",
                                                                "position": "fixed", "borderRadius": "25px"}}>
                    <bs.Row>
                        <bs.Col>
                            <h4>Sort by:</h4>
                        </bs.Col>
                    </bs.Row>
                    <p style={{"textDecoration": "underline"}}>Financial Quality</p>
                    
                    <bs.Row>
                        <bs.Col>
                            <Link
                                to={{
                                    pathname: `/sortBy`,
                                    state: {
                                        sortBy: 'avg_donation_per_donor',
                                        detail: results
                                    }
                                }}
                                
                                >
                                <p>Avg. donor contribution</p>
                            </Link>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col>
                            <Link
                            to={{
                                pathname: `/sortBy`,
                                state: {
                                    sortBy: 'percent_goal_complete',
                                    detail: results
                                }
                            }}
                        >
                            <p>% Goal Complete</p>
                        </Link>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col>
                            <Link
                                    to={{
                                        pathname: `/sortBy`,
                                        state: {
                                            sortBy: 'current_amount',
                                            detail: results
                                        }
                                    }}
                                >
                                    <p>Total $ Raised</p>
                            </Link>
                        </bs.Col>
                    </bs.Row>

                    <p className="mt-1" style={{"textDecoration": "underline"}}>Social Quality</p>
                    <bs.Row>
                        <bs.Col>
                            <Link
                            to={{
                                pathname: `/sortBy`,
                                state: {
                                    sortBy: 'campaign_hearts',
                                    detail: results
                                }
                            }}
                        >
                            <p># of Campaign hearts</p>
                        </Link>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col>
                        <Link
                        to={{
                            pathname: `/sortBy`,
                            state: {
                                sortBy: 'social_share_total',
                                detail: results
                            }
                        }}
                    >
                        <p>Shares on Social Media</p>
                    </Link>
                        </bs.Col>
                    </bs.Row>
                    <bs.Row>
                        <bs.Col>
                        <Link
                        to={{
                            pathname: `/sortBy`,
                            state: {
                                sortBy: 'overall_popularity',
                                detail: results
                            }
                        }}
                    >
                        <p>Overall Popularity</p>
                    </Link>
                        </bs.Col>
                    </bs.Row>

                </div>
            </bs.Col>
            <bs.Col>
            {rows.map((row, row_idx) => (
                <bs.Row key={row_idx}>
                    {row.map(campaign => (
                        <bs.Col md="3" key={campaign.campaign_id}>
                            <CampaignCard campaign={campaign} />
                        </bs.Col>
                    ))}
                </bs.Row>
                ))}
            </bs.Col>
                
        </bs.Row>
        
        </div>           
    )
}