import React from 'react'
import * as bs from 'react-bootstrap'
import AppContext from './context'
import { useLocation } from 'react-router-dom'
import { useEffect } from 'react'


function SearchResults(props) {
    return (
        <bs.Container>
            <SearchController />
        </bs.Container>
    )
}
export default SearchResults


const SearchController = props => {
    const location = useLocation();


    var results = []

    useEffect(() => {
        console.log(location.state.detail)
        results = location.state.detail;        
    })

    // Get the search results from the api
    var apiResults = (location.state.detail)

    return (
        <div>
            <bs.Container style={{"backgroundColor": "#96ceb4", "borderRadius": "25px"}}>
                <bs.Row>
                    <bs.Col>
                        <p className="text-center ml-4" style={{"fontSize": "20px"}}>
                        Total Estimated Donations:
                        </p>
                    </bs.Col>
                    <bs.Col>
                        <p className="text-center mr-5" style={{"fontSize": "20px"}}>
                        Total Estimated Donors:
                        </p>
                    </bs.Col>
                </bs.Row>
                <bs.Row>
                    <bs.Col md="1"></bs.Col>
                    <bs.Col style={{"backgroundColor": "white", "borderRadius": "25px"}}>
                        <h1 className="text-center mt-1" style={{"color": "#588c7e"}}>
                        ${apiResults[0]}
                        </h1>
                    </bs.Col>
                    <bs.Col md="1"></bs.Col>
                    <bs.Col style={{"backgroundColor": "white", "borderRadius": "25px"}}>
                        <h1 className="mt-1 text-center" style={{"color": "#588c7e"}}>
                        {apiResults[2]}
                        </h1>
                    </bs.Col>
                    <bs.Col md="1"></bs.Col>
                </bs.Row>
                <bs.Row>
                    <bs.Col>
                        <p className="text-center p-2 ml-5" style={{"fontSize": "20px"}}>
                            {apiResults[1]}% of your goal
                        </p>
                    </bs.Col>
                    <bs.Col>
                        <p className="text-center p-2 mr-5" style={{"fontSize": "20px"}}>
                            ${apiResults[3]} per donor on average
                        </p>
                    </bs.Col>
                </bs.Row>
            </bs.Container>
        </div>        
    )
}