import React from 'react'
import { useParams } from 'react-router-dom'
import AppContext from './context'
import Card from 'react-bootstrap/Card'
import * as bs from 'react-bootstrap'

function CampaignDetails(props) {
    const context = React.useContext(AppContext)
    let { id } = useParams()
    let myCamp = {}
   

    let campaignsArray = Object.values(context.campaigns)

    console.log(campaignsArray)

    for (var itemInner of campaignsArray) {
        if (itemInner.campaign_id === id) {
            myCamp = itemInner
            // Determine the average amount per donor
            myCamp['avg_donation_per_doner'] = parseFloat(myCamp['current_amount'])/parseFloat(myCamp['donators'])
            myCamp['avg_donation_per_doner'] = parseFloat(myCamp['avg_donation_per_doner']).toFixed(2);
        }
    }

    function numberWithCommas(x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    }

    

    return(
        <div>
            <bs.Row>
                <bs.Col>
                    <Card>
                        <Card.Header>
                            <h1><a href={myCamp['url']}>{myCamp['title']}</a></h1>
                        </Card.Header>
                        <Card.Img src={myCamp['campaign_image_url']} alt=''/>
                        
                    </Card>
                    
                   
                </bs.Col>
                <bs.Col md="4">
                    <Card className="mb-2">
                        <Card.Header>Creator Name: {myCamp['user_first_name']} {myCamp['user_last_name']}</Card.Header>
                        <Card.Body>
                            <blockquote className="blockquote mb-0">
                            <p className="p-1" style={{"fontSize": "15px"}}>
                               <span className="p-1" style={{"fontSize": "30px"}}>${numberWithCommas(parseInt(myCamp['current_amount']))}</span>  
                                raised of <span style={{"fontSize": "30px"}}>{numberWithCommas(parseInt(myCamp['goal']))}</span> goal
                            </p>
                            <p>
                                <bs.Row>
                                    <bs.Col>
                                        {parseInt(myCamp['percent_goal_complete']*100)}% of goal complete
                                    </bs.Col>
                                    <bs.Col>
                                        {parseInt(myCamp['donators'])} donors <br></br>
                                        ${myCamp['avg_donation_per_doner']} per donor
                                    </bs.Col>
                                </bs.Row>
                               
                            </p>
                            <bs.Row>
                                <bs.Col>
                                    <p>
                                        Likes: {parseInt(myCamp['campaign_hearts'])}
                                    </p>
                                </bs.Col>
                                <bs.Col>
                                    <p>
                                        Shares: {parseInt(myCamp['social_share_total'])}
                                    </p>
                                </bs.Col>
                            </bs.Row>
                            
                            </blockquote>
                        </Card.Body>
                    </Card>
                    <Card>
                        <Card.Body>
                            <p>Description: {myCamp['description']}</p>
                        </Card.Body>
                    </Card>
                </bs.Col>
            </bs.Row>
           
            
            <Card className="my-3">
                <Card.Header>
                    Additional Information
                </Card.Header>
                <Card.Body>
                    <p>Creation Date: {myCamp['created_at']}</p>
                    <p>Launch Date: {myCamp['launch_date']}</p>
                    <p>Days Active: {myCamp['days_active']}</p>
                    <p>Days Since Creation: {myCamp['days_created']}</p>
                    
                    <p>Overall Popularity: {myCamp['overall_popularity']}</p>
                    <p>City: {myCamp['location_city']}</p>
                    <p>Country: {myCamp['location_country']}</p>
                    <p>Zip Code: {myCamp['location_zip']}</p>
                    <p>Is this a charity?: {myCamp['is_charity']}</p>
                </Card.Body>
            </Card>
            

        </div>
    )
}
export default CampaignDetails